<template>
    <div :class="[this.color, 'arma-cont']">
        <h1>{{nombre}}</h1><br>
        <div class="info-arma">
            <img class="icon-arma" :src="require(`/public/assets/arma/${tipo}/${img_src}`)">
            <div class="details">
                <div>
                    <span>{{estrellas}}</span>
                </div><br>
                <router-link 
                    :to="{ name:'armaview' , 
                        params:{
                            nombre:this.nombre,rareza:this.rareza,tipo:this.tipo,img_src:this.img_src,
                            img_asc:this.img_asc,desc:this.desc,obtencion:this.obtencion,serie:this.serie
                        }}"
                    class="link">
                    Más info</router-link>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"CardArmaComponent",
    props:{
        nombre:String,
        rareza:Number,
        tipo:String,
        img_src: String,
        img_asc: String,
        desc: String,
        obtencion: String,
        serie:String
    },
    data(){
        return{
            estrellas:"",
            color:""
        }
    },
    mounted(){
        for(let i = 0 ; i < this.rareza; i++){
            this.estrellas +='⭐'
        }
        if(this.rareza==1)
            this.color="gris"
        else if(this.rareza==2)
            this.color="verde"
        else if(this.rareza==3)
            this.color="azul"
        else if(this.rareza==4)
            this.color="morado"
        else 
            this.color="dorado"
    }
}
</script>
<style lang="css" scoped>
    *{
        scroll-behavior: smooth;
    }
.arma-cont{
    background-color: rgba(255, 255, 255, 0.767);
    border:5px solid;
    border-radius:1em;
    box-shadow: -9px 10px 5px 0px rgba(0,0,0,0.45);
  -webkit-box-shadow: -9px 10px 5px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: -9px 10px 5px 0px rgba(0,0,0,0.45);
    color:black;
    display:block;
    height: 175px;
    width:110%;
}
.gris{
    border: 3px solid lightgray;
}
.verde{
    border: 3px solid rgb(176, 245, 176);
}
.azul{
    border: 3px solid rgb(70, 70, 243);
}
.morado{
    border: 3px solid purple;
}
.dorado{
    border: 3px solid goldenrod;
}
h1{
    font-size: 12px;
    margin:0 auto;
    margin-top: 15px;        
    text-align: center;
    width:70%;
}
.info-arma{
    display: flex;
}
.icon-arma{
    height: 100px;
    width:100px;
}
.details{
    display:block;
}
a{
    border-radius: 10%;
    background-color:rgb(192, 164, 4);
    color:black;
    padding:.5em;
    text-decoration: none;
    transition:all .7s ease-in-out;
}
a:hover{
    background-color:white;
    color:rgb(192, 164, 4);
}
@media screen and (max-width: 768px) {
    .arma-cont{
        border-radius:1em;
        color:black;
        display:block;
        height: fit-content;
        width:150px;
    }
    h1{
        font-size: 15px;
        margin-top: 10px;        
        text-align: center;
        width:100%;
    }

    .info-arma{
        display:block;
    }
    .icon-arma{
        height: 100px;
        width:100px;
    }
    .link{
        color:black;
        text-decoration: none;
    }
}
</style>