<template>
        <div class="elementos">
            <div class="elemento">
                <img :src="imagen" alt="{{nombre}}">
                <div class="info">
                    <span>{{nombre}}</span>
                    <button @click="customMostrar">Mostrar</button>
                </div>
            </div>
        </div>
</template>
<script>
export default {
    name:"ElementoMapa",
    props:{
        imagen:String,
        enlace:String,
        nombre:String
    },
    methods:{
        customMostrar(){
            this.$emit("customMostrar",this.enlace)
        }
    }

}
</script>
