      -webkit-box-shadow: -9px 10px 5px 0px rgba(0,0,0,0.45);
      -moz-box-shadow: -9px 10px 5px 0px rgba(0,0,0,0.45);
        color:black;
        display:block;
        padding: 1em;
        width:110%;
    }
    .elemento{
        display:flex;
    }
    img{
        height:75px;
        width:75px;
    }
    .info span{
        display:block;
    }
