<script>
import CharIconCardVue from '@/components/CardCharComponent.vue';
export default {
  name:"PersonajesView",
  components: {
    CharIconCardVue
  },
  data(){
        return{
            personajes:[],
            Mondstadt:[],
            Liyue:[],
            Inazuma:[],
            Sumeru:[],
            Anemo:[],Cryo:[],Dendro:[],Electro:[],Geo:[],Hydro:[],Pyro:[],
            mobile:null,
            windowWidth:null,
            showall:true,
            showMondstadt:false,
            showLiyue:false,
            showInazuma:false,
            showSumeru:false,
            showAnemo:false,
            showCryo:false,
            showDendro:false,
            showElectro:false,
            showGeo:false,
            showHydro:false,
            showPyro:false
        }
    },
  async mounted(){
    fetch("files/data.json").then(response => response.json()).then(result =>(this.separaPersonajes(result)))
    
  },
  created(){
        window.addEventListener("resize",this.checkScreen);
        this.checkScreen();
    },
  methods:{
      checkScreen(){
            this.windowWidth = window.innerWidth;
            if ( this.windowWidth <=750){
                this.mobile = true;
                return;
            }
            this.mobile = false;
            this.mobileNav = false;
            return;
        },
      separaPersonajes(result){
        this.personajes = result.personajes;
        if(this.personajes!=[])
        this.personajes.forEach(personaje => {
            if(personaje.region=="Mondstadt"){
              this.Mondstadt.push(Object.assign({}, personaje));
            }
            else if(personaje.region=="Liyue"){
              this.Liyue.push(Object.assign({}, personaje));
            }
            else if(personaje.region=="Inazuma"){
              this.Inazuma.push(Object.assign({}, personaje));
            }
            else if(personaje.region=="Sumeru"){
              this.Sumeru.push(Object.assign({}, personaje));
            }
        })
        this.personajes.forEach(personaje => {
            if(personaje.elemento=="Anemo"){
              this.Anemo.push(Object.assign({}, personaje));
            }
            else if(personaje.elemento=="Cryo"){
              this.Cryo.push(Object.assign({}, personaje));
            }
            else if(personaje.elemento=="Dendro"){
              this.Dendro.push(Object.assign({}, personaje));
            }
            else if(personaje.elemento=="Electro"){
              this.Electro.push(Object.assign({}, personaje));
            }
            else if(personaje.elemento=="Geo"){
              this.Geo.push(Object.assign({}, personaje));
            }
            else if(personaje.elemento=="Hydro"){
              this.Hydro.push(Object.assign({}, personaje));
            }
            else if(personaje.elemento=="Pyro"){
              this.Pyro.push(Object.assign({}, personaje));
            }
        })
      },
      show0(){
        this.showall = true;
        this.showInazuma = false;
        this.showLiyue = false;
        this.showSumeru = false;
        this.showMondstadt = false;
        this.showAnemo = false;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      show1(){
        this.showall = false;
        this.showInazuma = false;
        this.showLiyue = false;
        this.showSumeru = false;
        this.showMondstadt = !this.showMondstadt;
        this.showAnemo = false;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      show2(){
        this.showall = false;
        this.showMondstadt = false;
        this.showLiyue = false;
        this.showSumeru = false;
        this.showLiyue = !this.showLiyue;
        this.showAnemo = false;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      show3(){
        this.showall = false;
        this.showMondstadt = false;
        this.showLiyue = false;
        this.showSumeru = false;
        this.showInazuma = !this.showInazuma;
        this.showAnemo = false;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      show4(){
        this.showall = false;
        this.showMondstadt = false;
        this.showLiyue = false;
        this.showInazuma = false;
        this.showSumeru = !this.showSumeru;
        this.showAnemo = false;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      filterAnemo(){
        this.showall = false;
        this.showAnemo = !this.showAnemo;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      filterCryo(){
        this.showall = false;
        this.showCryo = !this.showCryo;
        this.showAnemo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      filterDendro(){
        this.showall = false;
        this.showDendro = !this.showDendro;
        this.showCryo = false;
        this.showAnemo = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      filterElectro(){
        this.showall = false;
        this.showElectro = !this.showElectro;
        this.showCryo = false;
        this.showDendro = false;
        this.showAnemo = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      filterGeo(){
        this.showall = false;
        this.showGeo = !this.showGeo;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showAnemo = false;
        this.showHydro = false;
        this.showPyro = false;
      },
      filterHydro(){
        this.showall = false;
        this.showHydro = !this.showHydro;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showAnemo = false;
        this.showPyro = false;
      },
      filterPyro(){
        this.showall = false;
        this.showPyro = !this.showPyro;
        this.showCryo = false;
        this.showDendro = false;
        this.showElectro = false;
        this.showGeo = false;
        this.showHydro = false;
        this.showAnemo = false;
      }
    }
}
