<template>
    <div :class="{'mobile-display' : mobile == true ,'normal-display' : mobile !=true }">
        <div class="cont-description">
            <img class="logo-princ" alt="Vue logo" src="../assets/logo.png">
            <div class="weapon-search" v-show="showAll">
              <a href="#filtroArco"><img @click="filtroArco" src="assets/icon_arma/Arco.png" alt=""></a>
              <a href="#filtroCatalizador"><img @click="filtroCatalizador" src="assets/icon_arma/Catalizador.png" alt=""></a>
              <a href="#filtroEspada"><img @click="filtroEspada" src="assets/icon_arma/Espada.png" alt=""></a>
              <a href="#filtroLanza"><img @click="filtroLanza" src="assets/icon_arma/Lanza.png" alt=""></a>
              <a href="#filtroMandoble"><img @click="filtroMandoble" src="assets/icon_arma/Mandoble.png" alt=""></a>
            </div>
            <button v-show="showAll==false" @click="show0" class="showAllBtn">
              Mostrar todas las armas
            </button>
            <p class="title">
              LISTA DE ARMAS
            </p>
            <div class="container-list"  v-show="showAll">
              <div class="arma-list" v-for="(arma,index) of armas" :key="index" >
                <CardArmaComponent
                :nombre="arma.nombre"
                :rareza="arma.rareza"
                :tipo="arma.tipo"
                :img_src="arma.img_src"
                :img_asc="arma.img_asc"
                :desc="arma.desc"
                :obtencion="arma.obtencion"
                :serie="arma.serie"
                />
              </div>
            </div>
            <div id="filtroArco" class="container-list" v-show="showArco">
              <div class="arma-list" v-for="(arma,index) of arcos" :key="index" >
                <CardArmaComponent
                :nombre="arma.nombre"
                :rareza="arma.rareza"
                :tipo="arma.tipo"
                :img_src="arma.img_src"
                :img_asc="arma.img_asc"
                :desc="arma.desc"
                :obtencion="arma.obtencion"
                :serie="arma.serie"
                />
              </div>
            </div>
            <div id="filtroCatalizador" class="container-list" v-show="showCatalizador">
              <div class="arma-list" v-for="(arma,index) of catalizadores" :key="index" >
                <CardArmaComponent
                :nombre="arma.nombre"
                :rareza="arma.rareza"
                :tipo="arma.tipo"
                :img_src="arma.img_src"
                :img_asc="arma.img_asc"
                :desc="arma.desc"
                :obtencion="arma.obtencion"
                :serie="arma.serie"
                />
              </div>
            </div>
            <div id="filtroEspada" class="container-list" v-show="showLanza">
              <div class="arma-list" v-for="(arma,index) of lanzas" :key="index" >
                <CardArmaComponent
                :nombre="arma.nombre"
                :rareza="arma.rareza"
                :tipo="arma.tipo"
                :img_src="arma.img_src"
                :img_asc="arma.img_asc"
                :desc="arma.desc"
                :obtencion="arma.obtencion"
                :serie="arma.serie"
                />
              </div>
            </div>
            <div id="filtroLanza" class="container-list" v-show="showEspada">
              <div class="arma-list" v-for="(arma,index) of espadas" :key="index" >
                <CardArmaComponent
                :nombre="arma.nombre"
                :rareza="arma.rareza"
                :tipo="arma.tipo"
                :img_src="arma.img_src"
                :img_asc="arma.img_asc"
                :desc="arma.desc"
                :obtencion="arma.obtencion"
                :serie="arma.serie"
                />
              </div>
            </div>
            <div id="filtroMandoble" class="container-list" v-show="showMandoble">
              <div class="arma-list" v-for="(arma,index) of mandobles" :key="index" >
                <CardArmaComponent
                :nombre="arma.nombre"
                :rareza="arma.rareza"
                :tipo="arma.tipo"
                :img_src="arma.img_src"
                :img_asc="arma.img_asc"
                :desc="arma.desc"
                :obtencion="arma.obtencion"
                :serie="arma.serie"
                />
              </div>
            </div>
        </div>
    </div>
</template>
<script>

import CardArmaComponent from '@/components/CardArmaComponent.vue'
export default {
  name: 'ArmasView',
  components: {
    CardArmaComponent
  },
  data(){
        return{
            armas:[],
            mobile:null,
            windowWidth:null,
            arcos:[],
            catalizadores:[],
            mandobles:[],
            lanzas:[],
            espadas:[],
            showAll:true,
            showArco:false,
            showMandoble:false,
            showLanza:false,
            showCatalizador:false,
            showEspada:false
        }
    },
  mounted(){
      fetch("files/data_a.json").then(response => response.json()).then(result =>(this.separaArmas(result)))
    },
  created(){
        window.addEventListener("resize",this.checkScreen);
        this.checkScreen();
    },
  methods:{
    checkScreen(){
          this.windowWidth = window.innerWidth;
          if ( this.windowWidth <=810){
              this.mobile = true;
              return;
          }
          this.mobile = false;
          this.mobileNav = false;
          return;
      },
    separaArmas(result){
      this.armas = result.armas;
      if(this.armas!=[])
      this.armas.forEach(arma => {
          if(arma.tipo=="Mandoble"){
            this.mandobles.push(Object.assign({}, arma));
          }
          else if(arma.tipo=="Lanza"){
            this.lanzas.push(Object.assign({}, arma));
          }
          else if(arma.tipo=="Catalizador"){
            this.catalizadores.push(Object.assign({}, arma));
          }
          else if(arma.tipo=="Espada"){
            this.espadas.push(Object.assign({}, arma));
          }
          else if(arma.tipo=="Arco"){
            this.arcos.push(Object.assign({}, arma));
          }
      })
    },
    show0(){
      this.showAll = true;
      this.showArco = false;
      this.showCatalizador = false;
      this.showLanza = false;
      this.showEspada = false;
      this.showMandoble = false;
    },
    filtroArco(){
      this.showAll = false;
      this.showArco = true;
      this.showCatalizador = false;
      this.showLanza = false;
      this.showEspada = false;
      this.showMandoble = false;
    },
    filtroCatalizador(){
      this.showAll = false;
      this.showArco = false;
      this.showCatalizador = true;
      this.showLanza = false;
      this.showEspada = false;
      this.showMandoble = false;
    },
    filtroEspada(){
      this.showAll = false;
      this.showArco = false;
      this.showCatalizador = false;
      this.showLanza = false;
      this.showEspada = true;
      this.showMandoble = false;
    },
    filtroLanza(){
      this.showAll = false;
      this.showArco = false;
      this.showCatalizador = false;
      this.showLanza = true;
      this.showEspada = false;
      this.showMandoble = false;
    },
    filtroMandoble(){
      this.showAll = false;
      this.showArco = false;
      this.showCatalizador = false;
      this.showLanza = false;
      this.showEspada = false;
      this.showMandoble = true;
    }
  }
}
</script>
<style lang="scss" scoped>
*{
    scroll-behavior: smooth;
}
    .mobile-display {
        .cont-description {
          background: url("https://thypix.com/wp-content/uploads/2021/04/genshin-impact-mobile-wallpaper-20-700x1400.jpg");
          background-attachment: fixed;
          background-position:top;
          background-repeat: no-repeat;
          background-size: cover;
          display:block;
          height:max-content;
          width: 100%;
          .logo-princ{
          height:20%;
          margin: 1em auto;
          width:100%;
        }
        .weapon-search {
          background-color: rgba(255, 255, 255, 0.5);
          display: flex;
          margin: .5em auto;
          width: 100%;
        }
        .weapon-search a{
          margin: 0 auto;
        }
        .weapon-search a img{
          margin: 0 auto;
          width:80%;
        }
        .title{
        
        background-color:antiquewhite;
        color:rgb(168, 127, 98);
        font-size:20px;
        font-weight: bold;
        margin: 0 auto;
        padding:20px;
        width:80%;
        }
        
      .showAllBtn{
        background-color:yellow;
        border: 5px goldenrod;
        color:goldenrod;
        display: block;
        font-family:'Amarante';
        font-size:30px;
        font-weight: bold;
        margin: 2em auto;
        text-align: center;
        text-decoration: none;
        width: 75%;
        :hover{
          background-color:white;
        }
      }
      .container-list{
        display:flex;
        flex-flow:wrap;
        justify-content: space-around;
        margin: 2em auto;
        margin-bottom: 0;
        width:100%;
        .arma-list{
          margin: 1em auto;
          width: auto;
        }
      }
  }
}
.normal-display{
  .cont-description{
    background: url("https://thypix.com/wp-content/uploads/2021/04/genshin-impact-mobile-wallpaper-20-700x1400.jpg");
    background-attachment: fixed;
    background-position:top;
    background-repeat: no-repeat;
    background-size: cover;
    display:block;
    height:max-content;
    width: 100%;
   
    .logo-princ{
      height:20%;
      margin: 1em auto;
      width:50%;
    }
    .weapon-search {
      background-color: rgba(255, 255, 255, 0.5);
      border-radius:1.5em;
      display: flex;
      margin: 2em auto;
      width: 90%;
    }
    .weapon-search a{
      margin: 0 auto;
    }
    
    .title{  
      background-color:antiquewhite;
      border: 3px solid rgb(168, 127, 98);
      color:rgb(168, 127, 98);
      font-size:20px;
      font-weight: bold;
      margin: 0 auto;
      padding:20px;
      width:80%;
    }
    
    .showAllBtn{
      background-color:yellow;
      border: 5px goldenrod;
      color:goldenrod;
      display: block;
      font-family:'Amarante';
      font-size:30px;
      font-weight: bold;
      margin: 2em auto;
      text-align: center;
      text-decoration: none;
      width: 75%;
      :hover{
        background-color:white;
      }
    }
    .container-list{
      display:grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      margin: 2em auto;
      margin-bottom: 0;
      width:100%;
      .arma-list{
        margin: 1em auto;
        width: auto;
      }
      @media screen and (max-width: 1108px){
        grid-template-columns: 1fr 1fr  1fr;
      }
      @media screen and (max-width: 836px){
        grid-template-columns: 1fr 1fr;
      }
    }
  }
}
</style>