<template>
    <div class="character">
        <div class="cont-char"><br><br><br>
            <div  :class="[color, 'img-cont']"><br>
                <div :class="[color, 'marco']">
                    <img :src="require(`/public/assets/arma/${tipo}/${img_src}`)"  alt="">
                </div><br>
                <div class="info-char">
                    <span>{{estrellas}}</span><br>
                    <span>{{nombre}}</span><br>
                    <span>{{desc}}</span><br><br>
                    <span>Modo de Obtención: {{obtencion}}</span><br>
                    <span>Serie de Armas: {{serie}}</span><br><br><br>
                    <div class="icons">
                        <img class="arma" :src="require(`/public/assets/icon_arma/${tipo}.png`)" alt="">
                        <img class="arma" :src="require(`/public/assets/arma/${tipo}/${img_asc}`)" alt="">
                    </div>
                </div>
                <router-link class="link" to="/armas">VOLVER</router-link>
            </div>
        </div><br><br><br>
    </div>
</template>
<script>
export default {
    name:"ArmaView",
    props:{
        nombre:String,
        rareza:Number,
        tipo:String,
        img_src: String,
        img_asc: String,
        desc: String,
        obtencion: String,
        serie:String
    },
    data(){
        return{
            estrellas:"",
            color:""
        }
    },
    mounted(){
        for(let i = 0 ; i < this.rareza; i++){
            this.estrellas +='⭐'
        }
        if(this.rareza==1)
            this.color="gris"
        else if(this.rareza==2)
            this.color="verde"
        else if(this.rareza==3)
            this.color="azul"
        else if(this.rareza==4)
            this.color="morado"
        else 
            this.color="dorado"
    },
}
</script>t
<style scoped>
@import url("https://fonts.googleapis.com/css?family=Amarante");
*{
    font-family:'Amarante';
    font-size:18px;
    scroll-behavior: smooth;
}
    .link{
        color:black;
        text-decoration: none;
    }
    .character{
        background: url("https://wallpaperaccess.com/full/5667869.jpg");
        background-attachment: fixed;
        background-position:top;
        background-repeat: no-repeat;
        background-size: cover;
        display:block;
        height:max-content;
        width: 100%;
    }
    .img-cont{
        border-radius:2em;
        display:block;
        margin: 0 auto;
        width:80%;
    }
    span{
        text-align:center;
        width:100%;
    }
    .info-char{
        margin: 1em auto;
        width:80%;
    }
    .icons{
        display:flex;
        justify-content:space-around;
        margin: 0 auto;
        width:80%;
    }
    .icons img{
        margin: 0 auto;
        height:100px;
        width:100px;
    }
    .arma{
        background-color:white;
        border-radius:50%;
    }
    .marco{
        background-color:#fff;
        border-radius:50%;
        height:150px;
        margin: 0 auto;
        width:150px;
    }
    .marco img{
        border-radius:50%;
        height:145px;
        margin: 0 auto;
        width:145px;
        
    }
    .gris{
        border: 3px solid lightgray;
        background-color: rgba(255, 255, 255, 0.500);
    }
    .verde{
        border: 3px solid rgb(176, 245, 176);
        background-color: rgba(255, 255, 255, 0.500);
    }
    .azul{
        border: 3px solid rgb(70, 70, 243);
        background-color: rgba(255, 255, 255, 0.500);
    }
    .morado{
        border: 3px solid purple;
        background-color: rgba(255, 255, 255, 0.500);
    }
    .dorado{
        border: 3px solid goldenrod;
        background-color: rgba(255, 255, 255, 0.500);
    }
</style>

